:root:not(.dark) main.contenedor.seccion.contenido-centrado > p, :root:not(.dark) main.contenedor.seccion.contenido-centrado p + p, body:not(.dark) main.contenedor.seccion.contenido-centrado > p, body:not(.dark) main.contenedor.seccion.contenido-centrado p + p {
